@import url('https://fonts.googleapis.com/css?family=Open+Sans:300');
@import url('https://fonts.googleapis.com/css?family=Roboto:300');

.App {
  text-align: center;
  background: rgb(185, 230, 255);
  background: -webkit-gradient(linear, left top, right bottom, from(rgb(185, 230, 255)), to(rgb(0, 166, 255)));
  background: -webkit-linear-gradient(top left, rgb(185, 230, 255), rgb(0, 166, 255));
  background: -o-linear-gradient(top left, rgb(185, 230, 255), rgb(0, 166, 255));
  background: linear-gradient(to bottom right, rgb(185, 230, 255), rgb(0, 166, 255));
  color: white;
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: 'Open Sans';
  text-shadow: 1px 1px grey;
}

.clock {
  font-size: 10vw;
  font-weight: 300;
  margin: 0;
}

.title {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.icon {
  font-size: 50px;
  padding-right: 10px;
}

*:focus{
  outline: none;
}

.textBox {
  background: none;
  color: white;
  border:none;
  border-bottom: 1px solid white;
  text-align: center;
  text-shadow: 1px 1px grey;
  margin-right: 5px;
  width: 75px;
}

.textBox::-webkit-input-placeholder {
  color: white;
}

#amPmBox {
  color: white;
}

.textOpt {
  color: black;
}

#submit {
  background: white;
  color: black;
  text-shadow: none;
  border: white 2px solid;
  cursor:pointer;
}
#submit:active {
  -webkit-transform: translateY(2px);
      -ms-transform: translateY(2px);
          transform: translateY(2px);
}

.form {
  padding-top: 50px;
}


